<template>
  <!--  -->
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input></el-col
        ><el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col></el-row
      >
      <!--  -->
      <el-table :data="infoList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="community_num" label="">
        </el-table-column>
        <el-table-column
          prop="community_belong_firehousenum"
          label=""
          width="180"
        >
        </el-table-column
        ><el-table-column prop="community_address_id" label="ID">
        </el-table-column
        ><el-table-column prop="community_name" label="">
        </el-table-column>

        <el-table-column prop="community_longitude" label="">
        </el-table-column>
        <el-table-column prop="community_latitude" label="">
        </el-table-column>

        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeInfoByNum(scope.row.community_num)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="editFormRules"
        ref="addFormRef"
        label-width="150px"
      >
        <el-form-item label="" prop="community_num">
          <el-input v-model="addForm.community_num"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="community_belong_firehousenum"
        >
          <el-input
            v-model="addForm.community_belong_firehousenum"
          ></el-input> </el-form-item
        ><el-form-item label="ID" prop="community_address_id">
          <el-input
            v-model="addForm.community_address_id"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="community_name">
          <el-input v-model="addForm.community_name"></el-input> </el-form-item
        ><el-form-item label="" prop="community_longitude">
          <el-input
            v-model="addForm.community_longitude"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="community_latitude">
          <el-input v-model="addForm.community_latitude"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addApartmentInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :rules="editFormRules"
        :model="editForm"
        label-width="150px"
      >
        <el-form-item label="" prop="community_num">
          <el-input
            v-model="editForm.community_num"
            disabled
          ></el-input> </el-form-item
        ><el-form-item
          label=""
          prop="community_belong_firehousenum"
        >
          <el-input v-model="editForm.community_belong_firehousenum"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="community_address_id">
          <el-input
            v-model="editForm.community_address_id"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="community_name">
          <el-input v-model="editForm.community_name"></el-input> </el-form-item
        ><el-form-item label="" prop="community_longitude">
          <el-input
            v-model="editForm.community_longitude"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="community_latitude">
          <el-input
            v-model="editForm.community_latitude"
          ></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editApartmentInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      infoList: [],
      addForm: {
        community_belong_firehousenum: '',
        community_address_id: '',
        community_name: '',
        community_num: '',
        community_longitude: '',
        community_latitude: '',
      },
      addFormRules: {},
      addDialogVisible: false,
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        community_num: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        community_belong_firehousenum: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        community_address_id: [
          {
            required: true,
            message: 'IDID',
            trigger: 'blur',
          },
        ],
        community_name: [
          { required: true, message: '', trigger: 'blur' },
        ],

        community_longitude: [
          { required: true, message: '', trigger: 'blur' },
        ],
        community_latitude: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post(
        '/community/allCommunity' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.infoList = res.data
      this.total = res.total
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addApartmentInfo() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return
        const community = this.addForm
        console.log(community)
        // 
        const { data: res } = await this.$axios.post(
          '/community/addCommunity',
          community
        )

        // if (res.status !== 0) {
        //   this.$message.error('')
        // }
        console.log(res)
        this.addDialogVisible = false
        this.getInfoList()
      })
    },

    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // 
    async editApartmentInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const community = this.editForm
        console.log(this.editForm)
        const { data: res } = await this.$axios.post(
          `/community/updateCommunity`,
          community
        )

        if (res.status !== 0) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getInfoList()
      })
    },

    // num
    async removeInfoByNum(num) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { data: res } = await this.$axios.post(
          '/community/deleteCommunity' + '?community_num=' + num
        )
        if (res.status !== 0) {
          return this.$message.error('')
        } else {
          this.getInfoList()
        }
      }
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getInfoList()
    },
    //   
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getInfoList()
    },
  },
}
</script>
<style lang="less" scoped>
</style>