<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input></el-col
      ></el-row>
      <!--  -->
      <el-table :data="infoList" style="width: 100%" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column
          prop="feedback_firewarning_id"
          label="ID"
          width="100"
        >
        </el-table-column>
        </el-table-column>
        <el-table-column
          prop="feedback_firecar_num"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="feedback_firehouse_name"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="feedback_fireman_name"
          label=""
          width="110"
        >
        </el-table-column>
        <el-table-column
          prop="feedback_fireman_latitude"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="feedback_fireman_longitude"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column prop="feedback_time" label="" width="180">
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      infoList: [],
      editInfoDialogVisible: false,
      editForm: {},
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post(
        '/feedback/allFirewarning' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.infoList = res.data
      this.total = res.total
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // Id
    async removeInfoById() {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        this.$message.success('')
        this.getInfoList()
      }
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getInfoList()
    },
    //   
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pageNum = newPage
      this.getInfoList()
    },
  },
}
</script>
<style lang="less" scoped>
</style>