<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input></el-col
      ></el-row>
      <!--  -->
      <el-table :data="infoList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column
          prop="fireman_username"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="fireman_realname"
          label=""
          width="140"
        >
        </el-table-column
        ><el-table-column
          prop="fireman_password"
          label=""
          width="150"
        >
        </el-table-column>
        <el-table-column prop="fireman_tel" label="" width="180">
        </el-table-column
        ><el-table-column
          prop="fireman_belong_firehouse"
          label=""
          width="160"
        >
        </el-table-column>
        <el-table-column prop="role" label="" width="160">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.role == 0"></el-tag>
            <el-tag type="info" v-else-if="scope.row.role == 1"></el-tag>
            <el-tag type="warning" v-else-if="scope.row.role == 2"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeInfoByName(scope.row.fireman_username)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        label-width="150px"
      >
        <el-form-item label="" prop="fireman_username">
          <el-input v-model="editForm.fireman_username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="fireman_realname">
          <el-input
            v-model="editForm.fireman_realname"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="fireman_password">
          <el-input v-model="editForm.fireman_password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="fireman_tel">
          <el-input v-model="editForm.fireman_tel"></el-input> </el-form-item
        ><el-form-item label="" prop="fireman_belong_firehouse">
          <el-input v-model="editForm.fireman_belong_firehouse"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <el-input v-model="editForm.role" disabled></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      infoList: [],
      editDialogVisible: false,
      editForm: {},
      //
      editFormRules: {
        fireman_username: [
          { required: true, message: '', trigger: 'blur' },
        ],
        fireman_password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur',
          },
        ],
        fireman_realname: [
          { required: true, message: '', trigger: 'blur' },
        ],
        fireman_tel: [
          { required: true, message: '', trigger: 'blur' },
        ],
        fireman_belong_firehouse: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        role: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post(
        '/fireman/allFireman' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.infoList = res.data
      this.total = res.total
    },
    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // 
    async editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const fireman = this.editForm

        const { data: res } = await this.$axios.post(`/fireman/update`, fireman)
        if (res.status !== 0) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getInfoList()
      })
    },

    // username
    async removeInfoByName(username) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { dat: res } = await this.$axios.post(
          '/fireman/delete' + '?username=' + username
        )
        if (res.status !== 0) {
          return this.$message.error('')
        }
        this.getInfoList()
      }
    },

    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getInfoList()
    },
    //   
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getInfoList()
    },
  },
}
</script>
<style lang="less" scoped>
</style>