<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input
        ></el-col>
      </el-row>

      <!--  -->
      <el-table :data="residentList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="resident_username" label="" width="100">
        </el-table-column>
        <el-table-column
          prop="resident_realname"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column prop="resident_password" label="" width="120">
        </el-table-column>
        <el-table-column prop="resident_tel" label="" width="140">
        </el-table-column>
        <el-table-column prop="resident_address" label="">
        </el-table-column>
        <el-table-column prop="resident_status" label="" width="120">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.resident_status"
              ></el-tag
            >
            <el-tag type="info" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserByName(scope.row.resident_username)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="80px"
      >
        <el-form-item label="" prop="resident_username">
          <el-input v-model="editForm.resident_username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_realname">
          <el-input v-model="editForm.resident_realname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_password">
          <el-input v-model="editForm.resident_password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_tel">
          <el-input v-model="editForm.resident_tel"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_address">
          <el-input v-model="editForm.resident_address"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_status">
          <el-input v-model="editForm.resident_status"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      residentList: [],
      editDialogVisible: false,
      editForm: {},
      //
      editFormRules: {
        resident_username: [
          { required: true, message: '', trigger: 'blur' },
        ],
        resident_address: [
          { required: true, message: '', trigger: 'blur' },
        ],
        resident_password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur',
          },
        ],
        resident_realname: [
          { required: true, message: '', trigger: 'blur' },
        ],
        resident_tel: [
          { required: true, message: '', trigger: 'blur' },
        ],
        resident_status: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      query: {},
      onStatus: 0,
    }
  },
  created() {
    this.getResidentList()
  },
  methods: {
    //
    async getResidentList() {
      const { data: res } = await this.$axios.post(
        '/user/allUser' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.$message.error('')
      } else {
        this.residentList = res.data
        this.total = res.total
      }
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
    },
    // 
    async editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const { data: res } = await this.$axios.post(
          `/user/update`,
          this.editForm
        )
        if (res.status !== 0) {
          return this.$message.error('')
        }
        console.log(this.editForm)
        this.editDialogVisible = false
        this.getResidentList()
      })
    },

    // username
    async removeUserByName(username) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { data: res } = await this.$axios.post(
          '/user/delete' + '?username=' + username
        )
        if (res.status !== 0) {
          return this.$message.error('')
        } else {
          this.getResidentList()
        }
      }
    },

    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getResidentList()
    },
    //   
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getResidentList()
    },
  },
}
</script>
<style lang="less" scoped>
</style>