<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getInfoList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="FindFirewarningByStatus(queryInfo.query)"
            ></el-button> </el-input></el-col
      ></el-row>

      <!--  -->
      <el-table :data="infoList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column
          prop="firewarning_community_num"
          label=""
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="firewarning_address_detail"
          label=""
          width="150"
        >
        </el-table-column>

        <el-table-column prop="firewarning_status" label="" width="110">
          <template slot-scope="scope"
            ><el-tag type="danger" v-if="scope.row.firewarning_status === 0"
              ></el-tag
            >
            <el-tag
              type="warning"
              v-else-if="scope.row.firewarning_status === 1"
              ></el-tag
            >
            <el-tag type="success" v-else></el-tag></template
          >
        </el-table-column>
        <el-table-column prop="firewarning_mp4" label="">
        </el-table-column>
        <el-table-column prop="firewarning_pic" label="">
        </el-table-column>

        <el-table-column
          prop="firewarning_situation"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column prop="firewarning_time" label="" width="180">
        </el-table-column
        ><el-table-column
          prop="firewarning_trap"
          label=""
          width="110"
        >
        </el-table-column
        ><el-table-column prop="firewarning_username" label="" width="80">
        </el-table-column>
        <el-table-column prop="firewarning_role" label="" width="100">
          <template slot-scope="scope"
            ><el-tag v-if="scope.row.firewarning_role === 0"></el-tag>
            <el-tag type="info" v-else-if="scope.row.firewarning_role === 1"
              ></el-tag
            >
            <el-tag type="warning" v-else-if="scope.row.firewarning_role == 2"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag></template
          >
        </el-table-column>

        <el-table-column
          prop="firewarning_longitude"
          label=""
          width="140"
        >
        </el-table-column>
        <el-table-column
          prop="firewarning_latitude"
          label=""
          width="140"
        >
        </el-table-column>
        <el-table-column
          prop="firewarning_tel"
          label=""
          width="150"
        >
        </el-table-column>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 2, 5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      infoList: [],
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post(
        '/firewarning/allFirewarning' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.infoList = res.data
      this.total = res.total
    },
    //
    async FindFirewarningByStatus(num) {
      if (num === '') {
        this.getInfoList()
      } else {
        const { data: res } = await this.$axios.post(
          `/firewarning/FirewarningByStatus` +
            '?pageNum=' +
            this.queryInfo.pageNum +
            '&pageSize=' +
            this.queryInfo.pageSize +
            '&status=' +
            num
        )
        console.log(res)
        if (res.code !== 200) {
          this.$message.error('')
        }
        this.infoList = res.data
      }
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getInfoList()
    },
    //   
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getInfoList()
    },
  },
}
</script>
<style lang="less" scoped>
</style>