<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input></el-col
        ><el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col></el-row
      >
      <!--  -->
      <el-table :data="infoList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="id" label="ID" width="120">
        </el-table-column>
        <el-table-column prop="province" label="" width="140">
        </el-table-column>
        <el-table-column prop="city" label="" width="140"> </el-table-column>
        <el-table-column prop="district" label="/" width="140">
        </el-table-column>
        <el-table-column prop="town" label="/" width="200">
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeInfoById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="editFormRules"
        ref="addFormRef"
        label-width="80px"
        ><el-form-item label="" prop="province">
          <el-input v-model="addForm.province"></el-input> </el-form-item
        ><el-form-item label="" prop="city">
          <el-input v-model="addForm.city"></el-input> </el-form-item
        ><el-form-item label="/" prop="district">
          <el-input v-model="addForm.district"></el-input> </el-form-item
        ><el-form-item label="/" prop="town">
          <el-input v-model="addForm.town"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addAddress"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        label-width="80px"
      >
        <el-form-item label="" prop="province">
          <el-input v-model="editForm.province"></el-input> </el-form-item
        ><el-form-item label="" prop="city">
          <el-input v-model="editForm.city"></el-input> </el-form-item
        ><el-form-item label="/" prop="district">
          <el-input v-model="editForm.district"></el-input> </el-form-item
        ><el-form-item label="/" prop="town">
          <el-input v-model="editForm.town"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editAddress"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      infoList: [],
      addDialogVisible: false,
      addForm: {
        province: '',
        city: '',
        district: '',
        town: '',
      },
      addFormRules: {},
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        province: [{ required: true, message: '', trigger: 'blur' }],
        city: [{ required: true, message: '', trigger: 'blur' }],
        district: [
          {
            required: true,
            message: '/',
            trigger: 'blur',
          },
        ],
        town: [
          {
            required: true,
            message: '/',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post(
        '/community_address/allCommunity_Address' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.message.error('')
      }
      this.infoList = res.data
      this.total = res.total
    },

    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },

    // 
    addAddress() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return
        const community_address = this.addForm
        // 
        const { data: res } = await this.$axios.post(
          '/community_address/addCommunity_Address',
          community_address
        )

        if (res.status !== 0) {
          this.$message.error('')
        }
        this.addDialogVisible = false
        this.getInfoList()
      })
    },

    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // 
    async editAddress() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const community_address = this.editForm
        const { data: res } = await this.$axios.post(
          `/community_address/updateCommunity_Address`,
          community_address
        )

        if (res.status !== 0) {
          return this.$message.error('')
        }
        this.editDialogVisible = false
        this.getInfoList()
      })
    },
    // id
    async removeInfoById(id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { data: res } = await this.$axios.post(
          '/community_address/deleteCommunity_Address' + '?id=' + id
        )
        if (res.status !== 0) {
          return this.$message.error('')
        } else {
          this.getInfoList()
        }
      }
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getInfoList()
    },
    //   
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pageNum = newPage
      this.getInfoList()
    },
  },
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 20px;
}
</style>