<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getInfoList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="FindFirehouseByNum(queryInfo.query)"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col></el-row
      >

      <!--  -->
      <el-table :data="infoList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column
        ><el-table-column prop="firehouse_num" label="" width="120">
        </el-table-column
        ><el-table-column prop="firehouse_name" label="" width="120">
        </el-table-column>
        <el-table-column
          prop="firehouse_address"
          label=""
          width="180"
        >
        </el-table-column>
        <el-table-column
          prop="firehouse_firecar_number"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="firehouse_fireman_number"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="firehouse_longitude"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="firehouse_latitude"
          label=""
          width="120"
        >
        </el-table-column>

        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeInfoByNum(scope.row.firehouse_num)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="editFormRules"
        ref="addFormRef"
        label-width="125px"
        ><el-form-item label="" prop="firehouse_num">
          <el-input v-model="addForm.firehouse_num"></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_name">
          <el-input v-model="addForm.firehouse_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="firehouse_address">
          <el-input
            v-model="addForm.firehouse_address"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_firecar_number">
          <el-input
            v-model="addForm.firehouse_firecar_number"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_fireman_number">
          <el-input v-model="addForm.firehouse_fireman_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="firehouse_longitude">
          <el-input
            v-model="addForm.firehouse_longitude"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_latitude">
          <el-input v-model="addForm.firehouse_latitude"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addHouse"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :rules="editFormRules"
        :model="editForm"
        label-width="125px"
      >
        <el-form-item label="" prop="firehouse_num">
          <el-input
            v-model="editForm.firehouse_num"
            disabled
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_name">
          <el-input v-model="editForm.firehouse_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="firehouse_address">
          <el-input
            v-model="editForm.firehouse_address"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_firecar_number">
          <el-input
            v-model="editForm.firehouse_firecar_number"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_fireman_number">
          <el-input
            v-model="editForm.firehouse_fireman_number"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firehouse_longitude">
          <el-input v-model="editForm.firehouse_longitude"></el-input>
        </el-form-item>
        <el-form-item label="" prop="firehouse_latitude">
          <el-input v-model="editForm.firehouse_latitude"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editHouseInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      infoList: [],
      addDialogVisible: false,
      addForm: {
        firehouse_num: '',
        firehouse_name: '',
        firehouse_address: '',
        firehouse_firecar_number: '',
        firehouse_fireman_number: '',
        firehouse_longitude: '',
        firehouse_latitude: '',
      },
      addFormRules: {},
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        firehouse_num: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        firehouse_name: [
          { required: true, message: '', trigger: 'blur' },
        ],
        firehouse_address: [
          { required: true, message: '', trigger: 'blur' },
        ],
        firehouse_firecar_number: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        firehouse_fireman_number: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        firehouse_longitude: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        firehouse_latitude: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post(
        '/firehouse/allFirehouse' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.infoList = res.data
      this.total = res.total
    },
    //
    async FindFirehouseByNum(num) {
      if (num === '') {
        this.getInfoList()
      } else {
        const { data: res } = await this.$axios.post(
          `/firehouse/FindFirehouseByNum?Firehouse_num=` + num
        )
        if (res.status !== 0) {
          {
            this.$message.error('')
          }
        }
        console.log(res)
        const findList = []
        findList[0] = res.data
        this.infoList = findList
      }
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addHouse() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return
        const firehouse = this.addForm
        // 
        const { data: res } = await this.$axios.post(
          '/firehouse/addFirehouse',
          firehouse
        )

        if (res.status !== 0) {
          this.$message.error('')
        }

        this.addDialogVisible = false
        this.getInfoList()
      })
    },

    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // 
    async editHouseInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const firehouse = this.editForm
        const { data: res } = await this.$axios.post(
          `/firehouse/updateFirehouse`,
          firehouse
        )

        if (res.status !== 0) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getInfoList()
        // 
      })
    },

    // num
    async removeInfoByNum(num) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { data: res } = await this.$axios.post(
          '/firehouse/deleteFirehouse' + '?Firehouse_num=' + num
        )
        if (res.status !== 0) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.getInfoList()
        }
      }
    },

    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getInfoList()
    },
    //   
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pageNum = newPage
      this.getInfoList()
    },
  },
}
</script>
<style lang="less" scoped>
</style>