<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getInfoList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="FindFirecarByNum(queryInfo.query)"
            ></el-button> </el-input></el-col
        ><el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col></el-row
      >

      <!--  -->
      <el-table :data="infoList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="firecar_num" label="" width="180">
        </el-table-column
        ><el-table-column
          prop="firecar_belong_firehouse"
          label=""
          width="180"
        >
        </el-table-column>
        <el-table-column prop="firecar_status" label="" width="110">
          <template slot-scope="scope">
            <el-tag type="info" v-if="scope.row === null"></el-tag>
            <el-tag type="success" v-else-if="scope.row.firecar_status === 0"
              ></el-tag
            >
            <el-tag type="warning" v-else-if="scope.row.firecar_status === 1"
              ></el-tag
            >
            <el-tag v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="apartment" label="">
        </el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeInfoByNum(scope.row.firecar_num)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="editFormRules"
        ref="addFormRef"
        label-width="130px"
      >
        <el-form-item label="" prop="firecar_num">
          <el-input v-model="addForm.firecar_num"></el-input>
        </el-form-item>
        <el-form-item label="" prop="firecar_belong_firehouse">
          <el-input
            v-model="addForm.firecar_belong_firehouse"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firecar_status">
          <el-input v-model="addForm.firecar_status"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addFirecar"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :rules="editFormRules"
        :model="editForm"
        label-width="130px"
      >
        <el-form-item label="" prop="firecar_num">
          <el-input
            v-model="editForm.firecar_num"
            disabled
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firecar_belong_firehouse">
          <el-input
            v-model="editForm.firecar_belong_firehouse"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="firecar_status">
          <el-input v-model="editForm.firecar_status"></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editFirecarInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
// TODO:()
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
      infoList: [],
      addDialogVisible: false,
      addForm: {
        firecar_num: '',
        firecar_belong_firehouse: '',
        firecar_status: '',
      },
      addFormRules: {},
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        firecar_num: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        firecar_belong_firehouse: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        firecar_status: [
          {
            required: true,
            message: '0/1/2',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post(
        '/firecar/allFirecar' +
          '?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize
      )
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.infoList = res.data
      this.total = res.total
    },
    //FIXME:
    //
    async FindFirecarByNum(num) {
      if (num === '') {
        this.getInfoList()
      } else {
        const { data: res } = await this.$axios.post(
          `/firecar/FindFirecarByNum?Firecar_num=` + num
        )
        if (res.status !== 0) {
          {
            this.$message.error('')
          }
        }
        const findList = []
        findList[0] = res.data
        this.infoList = findList
      }
    },

    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addFirecar() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return
        const firecar = this.addForm
        // 
        const { data: res } = await this.$axios.post(
          '/firecar/addFirecar',
          firecar
        )

        if (res.status !== 0) {
          this.$message.error('')
        }
        this.addDialogVisible = false
        this.getInfoList()
      })
    },
    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // 
    async editFirecarInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const firecar = this.editForm
        const { data: res } = await this.$axios.post(
          `/firecar/updateFirecar`,
          firecar
        )

        if (res.status !== 0) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getInfoList()
        // 
      })
    },

    // num
    async removeInfoByNum(num) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      // confirm cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { data: res } = await this.$axios.post(
          '/firecar/deleteFirecar' + '?Firecar_num=' + num
        )
        if (res.status !== 0) {
          return this.$message.error('')
        } else {
          this.getInfoList()
        }
      }
    },

    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getInfoList()
    },
    //   
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getInfoList()
    },
  },
}
</script>
<style lang="less" scoped>
</style>